import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: () => import('@/views/home')
  },
  {
    path: '/quiz',
    name: 'quiz',
    component: () => import('@/views/quiz')
  },
  {
    path: '/score',
    name: 'score',
    component: () => import('@/views/score')
  }
]

const router = new VueRouter({
  routes
})

router.beforeEach((to, from, next) => {
  if (to.name === 'home' || to.name === 'quiz') {
    document.body.style.backgroundImage = 'url(./images/homeBackground.jpg)'
    next()
  } else {
    document.body.style.backgroundImage = 'url(./images/scoreBackground.jpg)'
    next()
  }
})

export default router
