;(function (doc, win) {
  const htmlNode = doc.documentElement
  const resize = 'orientationchange' in win ? 'orientationchange' : 'resize'
  function resizeHtmlFontSize() {
    if (!htmlNode.clientWidth) return
    htmlNode.style.fontSize = 20 * (htmlNode.clientWidth / 320) + 'px'
  }
  if (!doc.addEventListener) return
  win.addEventListener(resize, resizeHtmlFontSize, false)
  doc.addEventListener('DOMContentLoaded', resizeHtmlFontSize, false)
})(document, window)
